@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
/*=============== GOOGLE FONTS ===============*/
:root {
    /*========== Font and typography ==========*/
    --body-font: "Poppins", sans-serif;
    --biggest-font-size: 2rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1.125rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
    /*========== Font weight ==========*/
    --font-semi-bold: 600;
    --font-bold: 700;
    /*========== Margenes ==========*/
    --mb-0-5: 0.5rem;
    --mb-0-75: 0.75rem;
    --mb-1: 1rem;
    --mb-1-5: 1.5rem;
    --mb-2: 2rem;
}

@media screen and (min-width: 968px) {
     :root {
        --biggest-font-size: 3rem;
        --h2-font-size: 1.75rem;
        --h3-font-size: 1.25rem;
        --normal-font-size: 1rem;
        --small-font-size: 0.875rem;
        --smaller-font-size: 0.813rem;
    }
}


/*=============== BASE ===============*/

*,
::before,
::after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    margin: 0;
    font-family: "Poppins", sans-serif;
    font-family: var(--body-font);
    transition: 0.5s;
}

h1,
h2,
h3 {
    font-weight: 600;
    font-weight: var(--font-semi-bold);
    color: var(--title-color);
    line-height: 1.5;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    height: auto;
}


/* --> global Classes */

@media screen and (min-width: 968px) {}


/*=============== LAYOUT ============   ===*/

.container {
    max-width: 1170px;
    margin: 0 auto;
}

.border {
    border: 1px solid gray;
}

.row {
    display: flex;
    align-items: center;
    justify-content: center;
}

.text-center {
    text-align: center;
}
/* profile heading */

select {
    border-radius: 5px;
    width: 90%;
    font-size: 1rem;
    font-weight: 600;
}

.container-user-info .user-info-row {
    justify-content: flex-start;
    margin-bottom: 0.5rem;
    padding: 1.75rem 1rem;
    border-radius: 5px;
}

.container-user-info .user-info-row-bg {
    background-color: rgb(248, 248, 248);
}

.container-user-info .border-no-bg {
    border: 1px solid rgb(240, 240, 240);
}

.user-info-row .user-info-col {
    width: 100%;
}

.user-info-col .user-info-field {}

.user-info-col .btn-user-info {
    margin: 0 auto;
    display: block;
    color: rgb(0, 0, 104);
    padding: 0.75rem 1.75rem;
    border-radius: 4px;
    font-weight: 600;
    border: transparent;
    background-color: rgb(240, 240, 240);
    font-size: 0.9rem;
    cursor: pointer;
    width: 70%;
}

.user-info-col .btn-user-info:active {
    background-color: rgb(231, 231, 231);
}

.user-info-col .btn-heading {
    color: rgb(0, 0, 104);
    background-color: transparent;
}

.heading-container-user-info {
    font-size: 1.07rem;
    font-weight: 600;
}
.tabs-component-main {
}
.tabs-component-main .row-tabs-component {
  justify-content: flex-start;
  margin: 1rem 0;
}
.row-tabs-component .btn-tab-name {
  background-color: transparent;
  color: #000;
  padding: 0.5rem 1.5rem;
  display: inline-block;
  border-radius: 100px;
  cursor: pointer;
  font-weight: 500;
  border: transparent;
  font-size: 1rem;
  /* border: 1px solid rgb(190, 190, 190); */
}
.row-tabs-component .btn-tab-name:first-child {
  margin-right: 0.5rem;
}
.row-tabs-component .btn-active-tab-name {
  background-color: rgb(75, 75, 75);
  color: #fff;
  /* border: transparent; */
}

.section-profile-lists {
    padding: 3rem 0;
}

.container-profile-lists hr {
    border-bottom: 1px solid rgb(27, 27, 27);
    margin: 0.5rem 0;
}

.container-profile-lists .header-profile-lists {
    justify-content: space-between;
}

.header-profile-lists {
    margin: 0.5rem 0;
}

.container-user-info .user-info-row {
    border: none;
}

select {
    border: 1px solid #ddd;
}


/* responsive  */

@media (max-width: 1199px) {
    .section-profile-lists {
        padding: 3rem;
    }
    .user-info-col .btn-user-info {
        width: 90%;
    }
    .container-user-info .user-info-row {
        padding: 1.75rem .5rem;
    }
}

@media (max-width: 575px) {
    .row {
        text-align: inherit;
        display: block;
    }
    .user-info-col .btn-user-info {
        margin-top: 1rem;
    }
    .input-field-main label,
    select {
        margin: .5rem 0;
    }
    .header-modal .btn-close-modal {
        position: absolute;
        top: 2%;
        right: 0;
    }
    .header-modal .btn-close-modal {
        padding-left: .5rem;
    }
}


/* end of responsive */
.modal-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(122, 122, 122, 0.24);
    z-index: 1;
    display: none;
}

.active-modal-bg {
    display: block;
}

.modal-main {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 70%;
    height: 90%;
    visibility: hidden;
    opacity: 0;
    z-index: -1;
    /* transition: 0.1s all; */
    display: flex;
    align-items: center;
}

@media (max-width: 500px) {
    .modal-main {
        width: 90%;
        height: 80%;
    }
}

.modal-active {
    opacity: 1;
    z-index: 1;
    visibility: visible;
}

.modal-main .modal-container {
    background-color: #fff;
    height: 100%;
    width: 100%;
}

.content-modal .header-modal {
    padding: 0.6rem 1rem;
    justify-content: space-between;
}

.header-modal .heading-header-modal {
    font-weight: 600;
    padding: 0.5rem 0;
}

.header-modal .btn-close-modal {
    background-color: rgb(94, 94, 94);
    border: transparent;
    height: 30px;
    color: #fff;
    width: 30px;
    border-radius: 50%;
    cursor: pointer;
    transition: 0.3s;
}

.btn-close-modal:hover {
    opacity: 0.8;
}

.content-modal .line-modal-header-end {
    width: 100%;
    border: transparent;
    border-bottom: 1px solid rgb(133, 133, 133);
}

@media (max-width: 575px) {
    .header-modal .heading-header-modal {
        font-size: 1rem;
    }
}
.form-main {
    width: 80%;
    padding: 1rem;
    margin: 0 auto;
}

.form-main .form-add-user {
    display: flex;
    flex-direction: column;
}

.input-field-main {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.5rem;
}

.input-field-main label,
select {
    padding: 0.75rem 1rem;
    background-color: transparent;
}

.input-field-main label {
    width: 30%;
}

.input-field-main input::-webkit-input-placeholder {
    color: rgba(0, 0, 0, 0.877);
}

.input-field-main input:-ms-input-placeholder {
    color: rgba(0, 0, 0, 0.877);
}

.input-field-main input::placeholder {
    color: rgba(0, 0, 0, 0.877);
}

.input-field-main label {
    font-size: 0.9rem;
    color: rgb(70, 70, 70);
}

.input-field {
    width: 70%;
    display: flex;
    flex-direction: column;
}

.input-field .error-inputField {
    color: rgb(255, 67, 67);
    font-size: 0.9rem;
    padding: 0.1rem 0;
}

.input-field input {
    width: 100%;
    padding: 0.75rem 1rem;
    border: 1px solid rgb(160, 160, 160);
    border-radius: 5px;
    background-color: transparent;
    font-size: 1rem;
}

.input-field select {
    width: 100%;
    border: 1px solid rgb(160, 160, 160);
    border-radius: 5px;
    text-transform: capitalize;
    font-size: 1rem;
}

.form-add-user .buttons-form {
    width: 60%;
    display: inline-flex;
    align-items: center;
    margin: 3rem auto 0 auto;
    justify-content: space-between;
}

.buttons-form button {
    padding: 1rem;
    width: 45%;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.buttons-form .btn-save {
    color: #fff;
    background-color: rgb(5, 3, 92);
}

.items-start {
    align-items: flex-start;
}

.active-error-input-margin {
    margin-bottom: 0.3rem;
}

@media (max-width: 767px) {
    .form-main {
        width: 100%;
    }
}

@media (max-width: 767px) {
    .form-add-user .buttons-form {
        width: 100%;
    }
}
.heading-main .heading {
  margin: 0.5rem auto;
}

.heading-userLists-main {
  justify-content: space-between;
}

.heading-userLists-main .btn-add-user {
  padding: 0.5rem 1rem;
  background-color: rgb(75, 75, 75);
  color: #fff;
  border: transparent;
  border-radius: 100px;
  font-weight: 600;
  text-transform: capitalize;
  cursor: pointer;
}
.btn-add-user span {
  margin: 0 0.5rem;
}

.justify-between {
  justify-content: space-between;
}

.profile-edit-main {}

.container-profile-edit {
    padding: 2rem;
    max-width: 1024px;
}

.container-profile-edit .heading-profile-edit {
    margin: 1rem auto;
}

.profile-edit-input-field-main {
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
}

.profile-edit-input-field-main label {
    width: 100%;
    padding: 0.5rem 0;
    font-weight: 600;
}

.profile-edit-input-field-main .fields {
    width: 100%;
}

.fields input,
.fields select {
    width: 50%;
    padding: 0.8rem;
    border: 1px solid rgb(221, 221, 221);
    border-radius: 5px;
    font-size: 1rem;
}

.fields input:focus {
    outline: none;
}

.fields input::-webkit-input-placeholder {
    color: rgb(196, 196, 196);
}

.fields input:-ms-input-placeholder {
    color: rgb(196, 196, 196);
}

.fields input::placeholder {
    color: rgb(196, 196, 196);
}

.fields input:nth-child(1) {
    border-right: none;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
}

.fields input:nth-child(2) {
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
}

.select-fields-profile-edit {
    padding: 0.5rem;
}

.select-field-labels {
    justify-content: space-between;
}

.select-field-labels label {
    width: 50%;
    font-weight: 600;
    padding: 0.5rem 0;
}

.select-fields select {
    width: 50%;
    padding: 0.8rem;
    border: 1px solid rgb(221, 221, 221);
    border-radius: 5px;
    font-size: 1rem;
    font-weight: normal;
}

select:focus {
    outline: none;
}

.select-fields select:nth-child(1) {
    border-right: none;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
}

.select-fields select:nth-child(2) {
    border-left: none;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
}

.buttons-profile-edit {
    width: 45%;
    justify-content: space-between;
    padding: 0.5rem;
}

.buttons-profile-edit .btn-profile-edit {
    width: 45%;
    padding: 1rem 0;
    text-align: center;
    border-radius: 5px;
    margin: 3rem 0;
    border: transparent;
    cursor: pointer;
    font-size: 1rem;
}

.buttons-profile-edit .btn-cancle {
    background-color: rgb(243, 243, 243);
}

a:active {
    color: #000;
}

.btn-cancle:active {
    background-color: rgb(223, 223, 223);
}

.buttons-profile-edit .btn-save {
    background-color: indigo;
    color: #fff;
    display: block;
    font-size: 1rem;
    line-height: 1.5rem;
}

.btn-save:focus {
    outline: none;
}

@media (max-width: 767px) {
    .buttons-profile-edit {
        width: 100%;
    }
}

@media (max-width: 575px) {
    .buttons-profile-edit .btn-profile-edit {
        display: block;
        width: 100%;
